.host {
  display: flex;
  align-items: center;
}

.label {
  min-width: 90px;
  padding: 0 8px 0 0;
}

.wrap {
  flex-grow: 1;
}

.input,
.textarea {
  width: 100%;
  padding: 4px 8px 6px;
  font-size: 20px;
  letter-spacing: 1px;
}

.input {
  &[type="color"] {
    height: 40px;
  }
}

.textarea {
  max-width: 100%;
  resize: vertical;
}
